<template>
  <div
    v-loading="isLoading"
    class="template-detail"
  >
    <el-table
      :data="detail"
      stripe
    >
      <el-table-column
        width="100"
      >
        <template slot-scope="scope">
          <div class="switch">
            <gs-switch
              v-model="scope.row.status"
              @change="changeTemplateStatus"
            />
            <gs-progress
              :percent="percent"
              :size="30"
              type="circle"
            />
          </div>
        </template>
      </el-table-column>
      <el-table-column
        v-for="item in tableConfig"
        :key="item.name"
        :label="item.name"
        align="center"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          {{ scope.row[item.propertyName] }}
        </template>
      </el-table-column>
      <el-table-column
        label="启动时间"
        align="center"
        width="120"
        prop="startTime"
        show-overflow-tooltip
      />
      <el-table-column
        align="center"
        prop="user"
        label="操作"
        width="140"
      >
        <template slot-scope="scope">
          <!-- <action-comfirm
            :title="'确定' + (scope.row.templateStatus === 1 ? '暂停' : '启动') +'该模板吗？'"
            @comfirm="changeTemplateStatus(scope)"
          >
            <gs-button
              slot="reference"
              type="text-primary"
            >{{ scope.row.templateStatus === 1 ? '暂停' : '启动' }}</gs-button>
          </action-comfirm> -->
          <gs-button
            :disabled="scope.row.templateStatus === (1 || 2)"
            type="text-primary"
            @click="editTemplate(scope)"
          >编辑</gs-button>
          <gs-button
            type="text-primary"
            @click="viewTemplate(scope)"
          >查看</gs-button>
          <action-comfirm
            title="确定删除该模板吗？"
            @comfirm="deleteTemplate()"
          >
            <gs-button
              slot="reference"
              type="text-primary"
            >删除</gs-button>
          </action-comfirm>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import './template-detail.scss';
import ActionComfirm from 'src/components/action-comfirm';
import * as dataQualityService from 'src/service/metadata-management/data-quality';
export default {
  components: {
    ActionComfirm
  },
  props: {
    templateInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },

  data() {
    return {
      isLoading: false,
      detail: [],
      percent: 0,
      tableConfig: [
        {
          name: '规则名称',
          propertyName: 'templateName'
        },
        {
          name: '生成周期',
          propertyName: 'buildTypeName'
        },
        {
          name: '表规则数量',
          propertyName: 'tableRulesNum'
        },
        {
          name: '字段规则数量',
          propertyName: 'columnRulesNum'
        },
        {
          name: '橙色警告数量',
          propertyName: 'orangeAlerts'
        },
        {
          name: '红色警告数量',
          propertyName: 'redAlerts'
        }
      ],
      interval: null
    };
  },

  // beforeMount() {
  //   this.getTemplateDetail();
  // },
  watch: {
    templateInfo: {
      handler: function (val) {
        if (Object.keys(val).length) {
          this.getTemplateDetail();
          this.polling();
        } else {
          this.detail = [];
          clearTimeout(this.interval);
          this.interval = null;
        }
      },
      immediate: true
    }
  },
  beforeDestroy() {
    clearTimeout(this.interval);
    this.interval = null;
  },
  unactivated() {
    clearTimeout(this.interval);
    this.interval = null;
  },
  methods: {
    copyTemplate(scope) {
      this.$emit('copyTemplate', scope.row.buildType);
    },
    editTemplate(scope) {
      this.$emit('editTemplate', scope.row.buildType);
    },
    viewTemplate(scope) {
      this.$emit('viewTemplate', scope.row.buildType);
    },
    /**
     * 删除模板
     */
    deleteTemplate() {
      this.isLoading = true;
      dataQualityService.deleteTemplate(this.templateInfo.templateId).then(res => {
        this.isLoading = false;
        this.$emit('handleDelete');
        this.$Message.success('删除模板成功', 1.5);
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 修改模板状态
     */
    changeTemplateStatus() {
      if ([1, 2].includes(this.templateInfo.templateStatus)) {
        this.isLoading = true;
        dataQualityService.closeTemplate(this.templateInfo.templateId).then(res => {
          this.isLoading = false;
          this.$Message.success(`关闭模板成功`, 1.5);
          this.$emit('handleChangeStatus');
        }).catch((res) => {
          this.isLoading = false;
        });
      } else if ([0, 3, 4].includes(this.templateInfo.templateStatus)) {
        this.isLoading = true;
        dataQualityService.openTemplate(this.templateInfo.templateId, this.templateInfo.templateStatus).then(res => {
          this.isLoading = false;
          this.$Message.success(`开启模板成功`, 1.5);
          this.$emit('handleChangeStatus');
        }).catch((res) => {
          this.isLoading = false;
        });
      }
    },
    getTemplateDetail() {
      const buildTypeName = this.templateInfo.buildType === 0 ? '周期生成' : '一次性生成';
      const status = this.templateInfo.templateStatus === 1 || this.templateInfo.templateStatus === 2;
      this.detail = [{...this.templateInfo, buildTypeName, status}];
      dataQualityService.getTemplatePercent(this.templateInfo.templateId).then(res => {
        this.percent = res ? ~~(res * 100) : 0;
      });
    },
    polling() {
      if (this.interval) {
        clearTimeout(this.interval);
      }
      this.interval = setTimeout(_ => {
        this.getTemplateDetail();
        this.polling();
      }, 8000);
    }
  }
};
</script>




